import { _decorator, Component, Sprite, Enum, Label, RichText } from 'cc';
import { ColorManager } from './ColorManager';
import { TelegramWebApp } from './telegram-web';
const { ccclass, property } = _decorator;

export enum ColorType {
    BgColor = 0,
    TextColor,
    HintColor,
    LinkColor,
    ButtonColor,
    ButtonTextColor,
    SecondaryBgColor,
    HeaderBgColor,
    BottomBarBgColor,
    AccentTextColor,
    SectionBgColor,
    SectionHeaderTextColor,
    SectionSeparatorColor,
    SubtitleTextColor,
    DestructiveTextColor
}

@ccclass('ColorSetter')
export class ColorSetter extends Component {

    @property
    isDefault: boolean = false;

    @property(Sprite)
    sprite: Sprite = null;

    @property(Label)
    label: Label = null;

    @property(RichText)
    richText: RichText = null;

    @property({ type: Enum(ColorType) })
    colorType: ColorType = ColorType.BgColor;

    start() {
        this.init();
    }

    async init(){

        while (!TelegramWebApp.Instance.isInited) {
            await new Promise(resolve => setTimeout(resolve, 100));
        }

        const colorManager = ColorManager.Instance;
        if (!colorManager) {
            console.error('ColorManager undefined');
            return;
        }

        let color = null;
        switch (this.colorType) {
            case ColorType.BgColor:
                color = colorManager.getBgColor(this.isDefault);
                break;
            case ColorType.TextColor:
                color = colorManager.getTextColor(this.isDefault);
                break;
            case ColorType.HintColor:
                color = colorManager.getHintColor(this.isDefault);
                break;
            case ColorType.LinkColor:
                color = colorManager.getLinkColor(this.isDefault);
                break;
            case ColorType.ButtonColor:
                color = colorManager.getButtonColor(this.isDefault);
                break;
            case ColorType.ButtonTextColor:
                color = colorManager.getButtonTextColor(this.isDefault);
                break;
            case ColorType.SecondaryBgColor:
                color = colorManager.getSecondaryBgColor(this.isDefault);
                break;
            case ColorType.HeaderBgColor:
                color = colorManager.getHeaderBgColor(this.isDefault);
                break;
            case ColorType.BottomBarBgColor:
                color = colorManager.getBottomBarBgColor(this.isDefault);
                break;
            case ColorType.AccentTextColor:
                color = colorManager.getAccentTextColor(this.isDefault);
                break;
            case ColorType.SectionBgColor:
                color = colorManager.getSectionBgColor(this.isDefault);
                break;
            case ColorType.SectionHeaderTextColor:
                color = colorManager.getSectionHeaderTextColor(this.isDefault);
                break;
            case ColorType.SectionSeparatorColor:
                color = colorManager.getSectionSeparatorColor(this.isDefault);
                break;
            case ColorType.SubtitleTextColor:
                color = colorManager.getSubtitleTextColor(this.isDefault);
                break;
            case ColorType.DestructiveTextColor:
                color = colorManager.getDestructiveTextColor(this.isDefault);
                break;
            default:
                console.error('Unknown type of color');
                break;
        }

        if (color) {
            if(this.sprite){
                this.sprite.color = color;
            }
            
            if(this.label){
                this.label.color = color;
            }

            if(this.richText){
                this.richText.fontColor = color;
            }
        }
    }
}