import { _decorator, sys } from 'cc';
const { ccclass, property } = _decorator;


const tgLoadPromise = new Promise<any>((resolve, reject) => {
    if (sys.platform === sys.Platform.MOBILE_BROWSER || sys.platform === sys.Platform.DESKTOP_BROWSER) {
        const script = document.createElement("script");
        script.src = "https://telegram.org/js/telegram-web-app.js";
        script.async = true;
        script.onload = () => {
            const intervalId = setInterval(() => {
                if ((window as any).Telegram && (window as any).Telegram.WebApp) {
                    resolve((window as any).Telegram.WebApp);
                    clearInterval(intervalId);
                }
            }, 100);
        };
        script.onerror = () => reject(new Error("Unable to load TelegramWebApp SDK, please check logs."));
        document.head.appendChild(script);
    }
});

export interface ThemeParams {
    bg_color: string;
    text_color: string;
    hint_color: string;
    link_color: string;
    button_color: string;
    button_text_color: string;
    secondary_bg_color: string;
    header_bg_color: string;
    bottom_bar_bg_color: string;
    accent_text_color: string;
    section_bg_color: string;
    section_header_text_color: string;
    section_separator_color: string;
    subtitle_text_color: string;
    destructive_text_color: string;
}

export interface WebAppUser {
    id: number;
    is_bot: boolean;
    first_name: string;
    last_name?: string;
    username?: string;
    language_code?: string;
    is_premium?: boolean;
    added_to_attachment_menu?: boolean;
    allows_write_to_pm?: boolean;
    photo_url?: string;
}
export interface WebAppChat {
    id: number;
    type: 'private' | 'group' | 'supergroup' | 'channel';
    title: string;
    username?: string;
    photo_url?: string;
}
export interface WebAppInitData {
    query_id?: string;
    user?: WebAppUser;
    receiver?: WebAppUser;
    chat?: WebAppChat;
    chat_type?: 'private' | 'group' | 'supergroup' | 'channel';
    chat_instance?: string;
    start_param?: string;
    can_send_after?: number;
    auth_data: number;
    hash: string;
}

export interface CloudStorage {
    setItem(key: string, value: string, callback?: (error: any, success: boolean) => void): CloudStorage;
    getItem(key: string, callback: (error: any, value: string) => void): CloudStorage;
    getItems(keys: string[], callback: (error: any, values: { [key: string]: string }) => void): CloudStorage;
    removeItem(key: string, callback?: (error: any, success: boolean) => void): CloudStorage;
    removeItems(keys: string[], callback?: (error: any, success: boolean) => void): CloudStorage;
    getKeys(callback: (error: any, keys: string[]) => void): CloudStorage;
}

@ccclass('TelegramWebApp')
export class TelegramWebApp {
    private static _instance: TelegramWebApp;
    private constructor() {

    }
    public static get Instance(): TelegramWebApp {
        if (!TelegramWebApp._instance) {
            TelegramWebApp._instance = new TelegramWebApp();
        }
        return TelegramWebApp._instance;
    }

    private _tgWebAppJS: any = null;
    public async init(): Promise<{ success: boolean }> {
        this._tgWebAppJS = await tgLoadPromise;

        if (this._tgWebAppJS) {
            return Promise.resolve({ success: true });
        } else {
            return Promise.resolve({ success: false });
        }
    }

    public openTelegramLink(url: string) {
        if (!this._tgWebAppJS) {
            console.error("telegram web app is not inited!");
            return;
        }
        this._tgWebAppJS.openTelegramLink(url);
    }

    public share(url: string, text?: string) {
        const shareUrl = 'https://t.me/share/url?url=' + url + '&' + new URLSearchParams({ text: text || '' }).toString();
        this.openTelegramLink(shareUrl);
    }

    public shareToStory(media_url: string, shareText?: string, widget_link_url?: string, widget_link_name?: string) {
        if (!this._tgWebAppJS) {
            console.error("telegram web app is not inited!");
            return;
        }
        const widget_link = {
            text: shareText,
            widget_link: {
                url: widget_link_url,
                name: widget_link_name,
            },
        };
        this._tgWebAppJS.shareToStory(media_url, widget_link);
    }

    public getTelegramWebApp() {
        return this._tgWebAppJS;
    }

    public getTelegramThemeParams(): ThemeParams {
        if (!this._tgWebAppJS) {
            console.error("telegram web app is not inited!");
            return null;
        }
        return this._tgWebAppJS.themeParams;
    }

    public getTelegramWebAppInitData(): WebAppInitData {
        if (!this._tgWebAppJS) {
            console.error("telegram web app is not inited!");
            return null;
        }
        return this._tgWebAppJS.initDataUnsafe;
    }

    public getTelegramUser(): WebAppUser {
        if (!this._tgWebAppJS) {
            console.error("telegram web app is not inited!");
            return null;
        }
        return this._tgWebAppJS.initDataUnsafe.user;
    }

    public doHapticImpact(style: string){
        if (!this._tgWebAppJS) {
            console.error("telegram web app is not inited!");
            return null;
        }
        return this._tgWebAppJS.HapticFeedback.impactOccurred(style);
    }

    public doHapticNotification(type: string){
        if (!this._tgWebAppJS) {
            console.error("telegram web app is not inited!");
            return null;
        }
        return this._tgWebAppJS.HapticFeedback.notificationOccurred(type);
    }

    public getTelegramInitData(): string {
        if (!this._tgWebAppJS) {
            console.error("telegram web app is not inited!");
            return null;
        }
        return this._tgWebAppJS.initData;
    }

    public openInvoice(url: string, callback: any) {
        if (!this._tgWebAppJS) {
            console.error("telegram web app is not inited!");
            return null;
        }
        this._tgWebAppJS.openInvoice(url, callback);
    }

    public alert(message: string) {
        this._tgWebAppJS.showAlert(message);
    }

    public confirm(message: string, callback: any){
        this._tgWebAppJS.showConfirm(message, callback);
    }

    //For cloudStorage
    private get cloudStorage(): CloudStorage {
        if (!this._tgWebAppJS) {
            console.error("Telegram Web App is not initialized!");
            return null;
        }
        if (!this._tgWebAppJS.cloudStorage) {
            console.error("CloudStorage is not available!");
            return null;
        }
        return this._tgWebAppJS.cloudStorage;
    }

    public cloudStorageSetItem(key: string, value: string, callback?: (error: any, success: boolean) => void): void {
        const storage = this.cloudStorage;
        if (!storage) return;
        storage.setItem(key, value, callback);
    }

    public cloudStorageGetItem(key: string, callback: (error: any, value: string) => void): void {
        const storage = this.cloudStorage;
        if (!storage) return;
        storage.getItem(key, callback);
    }

    public cloudStorageGetItems(keys: string[], callback: (error: any, values: { [key: string]: string }) => void): void {
        const storage = this.cloudStorage;
        if (!storage) return;
        storage.getItems(keys, callback);
    }

    public cloudStorageRemoveItem(key: string, callback?: (error: any, success: boolean) => void): void {
        const storage = this.cloudStorage;
        if (!storage) return;
        storage.removeItem(key, callback);
    }

    public cloudStorageRemoveItems(keys: string[], callback?: (error: any, success: boolean) => void): void {
        const storage = this.cloudStorage;
        if (!storage) return;
        storage.removeItems(keys, callback);
    }

    public cloudStorageGetKeys(callback: (error: any, keys: string[]) => void): void {
        const storage = this.cloudStorage;
        if (!storage) return;
        storage.getKeys(callback);
    }

    public get isInited(): boolean {
        return this._tgWebAppJS != null;
    }
}