import { _decorator, Component, Node, Vec3, tween, UITransform, Enum } from 'cc';
const { ccclass, property } = _decorator;

enum PopupState {
    None,
    Hidden,
    Showing,
    Shown,
    Hiding,
}

enum SlideDirection {
    Left,
    Right,
    Top,
    Bottom,
}

@ccclass('SlidePopup')
export class SlidePopup extends Component {
    @property({ type: Enum(SlideDirection) })
    showDirection: SlideDirection = SlideDirection.Left;

    @property({ type: Enum(SlideDirection) })
    hideDirection: SlideDirection = SlideDirection.Right;

    @property
    showDuration: number = 0.5;

    @property
    hideDuration: number = 0.5;

    @property
    isOpenAtStart: boolean = false;

    private state: PopupState = PopupState.None;

    start() {
        this.initPosition();
    }

    initPosition() {
        if (this.isOpenAtStart) {
            this.node.setPosition(new Vec3(0, 0, 0));
            this.state = PopupState.Shown;
        } else {
            const startPos = this.getStartPosition(this.hideDirection);
            this.node.setPosition(startPos);
            this.state = PopupState.Hidden;
        }
    }

    getStartPosition(direction: SlideDirection): Vec3 {
        const canvasSize = this.node.parent!.getComponent(UITransform)!.contentSize;

        switch (direction) {
            case SlideDirection.Left:
                return new Vec3(-canvasSize.width, 0, 0);
            case SlideDirection.Right:
                return new Vec3(canvasSize.width, 0, 0);
            case SlideDirection.Top:
                return new Vec3(0, canvasSize.height, 0);
            case SlideDirection.Bottom:
                return new Vec3(0, -canvasSize.height, 0);
            default:
                return new Vec3(0, 0, 0);
        }
    }

    show() {
        if (this.state !== PopupState.Hidden && this.state !== PopupState.Hiding && this.state !== PopupState.None) {
            return;
        }
        this.state = PopupState.Showing;

        const startPos = this.getStartPosition(this.showDirection);
        this.node.setPosition(startPos);

        tween(this.node)
            .to(this.showDuration, { position: new Vec3(0, 0, 0) }, { easing: 'sineOut' })
            .call(() => {
                this.state = PopupState.Shown;
            })
            .start();
    }

    hide() {
        if (this.state !== PopupState.Shown && this.state !== PopupState.Showing) {
            return;
        }
        this.state = PopupState.Hiding;

        const endPos = this.getStartPosition(this.hideDirection);

        tween(this.node)
            .to(this.hideDuration, { position: endPos }, { easing: 'sineIn' })
            .call(() => {
                this.state = PopupState.Hidden;
            })
            .start();
    }
}
