import { _decorator, Component, EventHandler, Label } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('TypewriterEffect')
export class TypewriterEffect extends Component {

    @property({ type: Label, tooltip: 'Label to display the text' })
    label: Label = null;

    @property({ multiline: true, tooltip: 'Full text to display' })
    fullText: string = '';

    @property({ tooltip: 'Total time to display the text (in seconds)' })
    totalTime: number = 5;

    @property({ tooltip: 'Pause after period (in seconds)' })
    pauseAfterPeriod: number = 0.5;

    @property({ tooltip: 'Pause after comma (in seconds)' })
    pauseAfterComma: number = 0.3;

    @property({ tooltip: 'Start typing automatically on start' })
    startsOnStart: boolean = false;

    @property({ tooltip: 'Delay before invoking the event (in seconds)' })
    delayBeforeEvent: number = 0;

    @property({ type: [EventHandler], tooltip: 'Event to call when typing is complete' })
    onTypingComplete: EventHandler[] = [];

    private _isTyping: boolean = false;

    onLoad() {
        if (this.startsOnStart) {
            this.ShowText();
        }
    }

    public ShowText() {
        if (this._isTyping) {
            return;
        }
        this._isTyping = true;
        this.label.string = '';
        this.typeText();
    }

    private async typeText() {
        const characters = this.fullText.split('');
        const totalCharacters = characters.length;
        const baseInterval = this.totalTime / totalCharacters;

        for (let i = 0; i < totalCharacters; i++) {
            const char = characters[i];
            this.label.string += char;

            if (char === '.') {
                await this.sleep(this.pauseAfterPeriod * 1000);
            } else if (char === ',') {
                await this.sleep(this.pauseAfterComma * 1000);
            } else {
                await this.sleep(baseInterval * 1000);
            }
        }
        this._isTyping = false;

        await this.sleep(this.delayBeforeEvent * 1000);

        Component.EventHandler.emitEvents(this.onTypingComplete);
    }

    private sleep(ms: number) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }
}
