import { _decorator, Component, tween, Vec3, Node, CCFloat } from 'cc';
import VibrationManager from './VibrationManager';
const { ccclass, property } = _decorator;

@ccclass('ButtonFeedbacks')
export class ButtonFeedbacks extends Component {
    @property
    useHoverTween = true;

    @property
    useClickTween = true;

    @property({ type: CCFloat, tooltip: "Hover reduction time (in seconds)" })
    hoverDuration: number = 0.2;

    @property({tooltip: "How many times to reduce when hovering" }) 
    hoverScale: Vec3 = new Vec3(0.95, 0.95, 0.95);

    @property({ type: CCFloat, tooltip: "Time to decrease when pressed (in seconds)" })
    clickDuration: number = 0.1;

    @property({tooltip: "How many times to reduce when pressed" })
    clickScale: Vec3 = new Vec3(0.9, 0.9, 0.9);

    @property({tooltip: "How much to increase after clicking" }) 
    clickScaleUp: Vec3 = new Vec3(1.1, 1.1, 1.1);

    private originalScale: Vec3 = new Vec3();

    start() {
        this.originalScale.set(this.node.scale);

        this.node.on(Node.EventType.MOUSE_ENTER, this.onMouseEnter, this);
        this.node.on(Node.EventType.MOUSE_LEAVE, this.onMouseLeave, this);
        this.node.on(Node.EventType.TOUCH_END, this.onClick, this);
    }

    onMouseEnter() {
        if(!this.useHoverTween) return;
        tween(this.node)
            .to(this.hoverDuration, { scale: this.hoverScale })
            .start();
    }

    onMouseLeave() {
        if(!this.useHoverTween) return;
        tween(this.node)
            .to(this.hoverDuration, { scale: this.originalScale })
            .start();
    }

    onClick() {
        if(!this.useClickTween) return;
        VibrationManager.soft();
        tween(this.node)
            .to(this.clickDuration, { scale: this.clickScale })
            .to(this.clickDuration/2, { scale: this.clickScaleUp })
            .to(this.clickDuration, { scale: this.originalScale })
            .start();
    }
}
