import { _decorator, Component, Node } from 'cc';
import { TelegramWebApp } from './telegram-web';
const { ccclass, property } = _decorator;

@ccclass('VibrationManager')
export default class VibrationManager {
    private static isVibrationSupported(): boolean {
        return TelegramWebApp.Instance != null;
    }

    public static light(): void {
        if (this.isVibrationSupported()) {
            TelegramWebApp.Instance.doHapticImpact("light");
        }
    }

    public static medium(): void {
        if (this.isVibrationSupported()) {
            TelegramWebApp.Instance.doHapticImpact("medium");
        }
    }

    public static heavy(): void {
        if (this.isVibrationSupported()) {
            TelegramWebApp.Instance.doHapticImpact("heavy");
        }
    }

    public static rigid(): void {
        if (this.isVibrationSupported()) {
            TelegramWebApp.Instance.doHapticImpact("rigid");
        }
    }

    public static soft(): void {
        if (this.isVibrationSupported()) {
            TelegramWebApp.Instance.doHapticImpact("soft");
        }
    }

    public static error(): void {
        if (this.isVibrationSupported()) {
            TelegramWebApp.Instance.doHapticNotification("error");
        }
    }

    public static success(): void {
        if (this.isVibrationSupported()) {
            TelegramWebApp.Instance.doHapticNotification("success");
        }
    }

    public static warning(): void {
        if (this.isVibrationSupported()) {
            TelegramWebApp.Instance.doHapticNotification("warning");
        }
    }
}
