import { _decorator, Component, Label } from 'cc';
import { TelegramWebApp } from './telegram-web';
const { ccclass, property } = _decorator;

@ccclass('TestTgFutures')
export class TestTgFutures extends Component {
    @property(Label)
    answer: Label = null;

    public showConfirm(){
        const message = "    .     .";
        TelegramWebApp.Instance.confirm(message, this.onConfirmAnswered.bind(this));
    }

    onConfirmAnswered(confirm: boolean){
        if(confirm){
            this.onConfirmed();
        }
        else{
            this.onChaneled();
        }
    }

    onConfirmed(){
        this.answer.string = "true";
    }

    onChaneled(){
        this.answer.string = "false";
    }
    
}

