import { TelegramWebApp } from './telegram-web';
import { UserMainData } from './UserInterfaces';

export class UserDataManager {
    private static _instance: UserDataManager;

    private constructor() {}

    public static get Instance(): UserDataManager {
        if (!UserDataManager._instance) {
            UserDataManager._instance = new UserDataManager();
        }
        return UserDataManager._instance;
    }

    public saveUserData(data: UserMainData, callback?: (error: any, success: boolean) => void): void {
        const telegramWebApp = TelegramWebApp.Instance;
        if (!telegramWebApp.isInited) {
            console.error("Telegram Web App  !");
            if (callback) callback(new Error("Telegram Web App  !"), false);
            return;
        }
        const dataString = JSON.stringify(data);

        telegramWebApp.cloudStorageSetItem('user_main_data', dataString, callback);
    }

    public getUserData(callback: (error: any, data: UserMainData | null) => void): void {
        const telegramWebApp = TelegramWebApp.Instance;
        if (!telegramWebApp.isInited) {
            console.error("Telegram Web App  !");
            callback(new Error("Telegram Web App  !"), null);
            return;
        }

        telegramWebApp.cloudStorageGetItem('user_main_data', (error, value) => {
            if (error) {
                console.error("    :", error);
                callback(error, null);
            } else if (value) {
                const data: UserMainData = JSON.parse(value);
                callback(null, data);
            } else {
                callback(null, null);
            }
        });
    }

    public updateUserData(partialData: Partial<UserMainData>, callback?: (error: any, success: boolean) => void): void {
        this.getUserData((error, data) => {
            if (error) {
                if (callback) callback(error, false);
                return;
            }

            const updatedData: UserMainData = {
                ...data,
                ...partialData
            };

            this.saveUserData(updatedData, callback);
        });
    }

    public removeUserData(callback?: (error: any, success: boolean) => void): void {
        const telegramWebApp = TelegramWebApp.Instance;
        if (!telegramWebApp.isInited) {
            console.error("Telegram Web App  !");
            if (callback) callback(new Error("Telegram Web App  !"), false);
            return;
        }

        telegramWebApp.cloudStorageRemoveItem('user_main_data', callback);
    }
}
