import { _decorator, Color, Component, Node } from 'cc';
import { TelegramWebApp, ThemeParams } from "./telegram-web";
const { ccclass, property } = _decorator;

@ccclass('ColorManager')
export class ColorManager extends Component {

    private static _instance: ColorManager = null;

    public static get Instance(): ColorManager {
        return this._instance;
    }

    userThemeParams: ThemeParams = null;

    defaultThemeParams: ThemeParams = {
        bg_color: "FFFFFF",
        text_color: "000000",
        hint_color: "8A8A8E",
        link_color: "007AFF",
        button_color: "5AC8FB",
        button_text_color: "FFFFFF",
        secondary_bg_color: "F2F2F7",
        header_bg_color: "F9F9F9",
        bottom_bar_bg_color : "FF7171",
        accent_text_color: "007AFF",
        section_bg_color: "FFFFFF",
        section_header_text_color: "85858B",
        section_separator_color: "D4D4D5",
        subtitle_text_color: "8A8A8E",
        destructive_text_color: "FF3B30"
    }

    protected onLoad(): void {
        if (ColorManager._instance === null) {
            ColorManager._instance = this;
            TelegramWebApp.Instance.init().then(() => {
                this.init();
            }).catch((error) => {
                console.error('Failed to initialize TelegramWebApp:', error);
            });
        } else {
            this.destroy();
            console.warn('Multiple instances of ColorManager detected.');
        }
    }

    async init(){
        while (!TelegramWebApp.Instance.isInited) {
            await new Promise(resolve => setTimeout(resolve, 100));
        }
        this.userThemeParams = TelegramWebApp.Instance.getTelegramThemeParams();
    }

    public getBgColor(isDefault: boolean = false): Color{
        if(!this.userThemeParams || isDefault){
            return new Color(this.defaultThemeParams.bg_color);
        }
        return new Color(this.userThemeParams.bg_color);
    }
    public getTextColor(isDefault: boolean = false): Color{
        if(!this.userThemeParams || isDefault){
            return new Color(this.defaultThemeParams.text_color);
        }
        return new Color(this.userThemeParams.text_color);
    }
    public getHintColor(isDefault: boolean = false): Color{
        if(!this.userThemeParams || isDefault){
            return new Color(this.defaultThemeParams.hint_color);
        }
        return new Color(this.userThemeParams.hint_color);
    }
    public getLinkColor(isDefault: boolean = false): Color{
        if(!this.userThemeParams || isDefault){
            return new Color(this.defaultThemeParams.link_color);
        }
        return new Color(this.userThemeParams.link_color);
    }
    public getButtonColor(isDefault: boolean = false): Color{
        if(!this.userThemeParams || isDefault){
            return new Color(this.defaultThemeParams.button_color);
        }
        return new Color(this.userThemeParams.button_color);
    }
    public getButtonTextColor(isDefault: boolean = false): Color{
        if(!this.userThemeParams || isDefault){
            return new Color(this.defaultThemeParams.button_text_color);
        }
        return new Color(this.userThemeParams.button_text_color);
    }
    public getSecondaryBgColor(isDefault: boolean = false): Color{
        if(!this.userThemeParams || isDefault){
            return new Color(this.defaultThemeParams.secondary_bg_color);
        }
        return new Color(this.userThemeParams.secondary_bg_color);
    }
    public getHeaderBgColor(isDefault: boolean = false): Color{
        if(!this.userThemeParams || isDefault){
            return new Color(this.defaultThemeParams.header_bg_color);
        }
        return new Color(this.userThemeParams.header_bg_color);
    }
    public getBottomBarBgColor(isDefault: boolean = false): Color{
        if(!this.userThemeParams || isDefault){
            return new Color(this.defaultThemeParams.bottom_bar_bg_color);
        }
        return new Color(this.userThemeParams.bottom_bar_bg_color);
    }
    public getAccentTextColor(isDefault: boolean = false): Color{
        if(!this.userThemeParams || isDefault){
            return new Color(this.defaultThemeParams.accent_text_color);
        }
        return new Color(this.userThemeParams.accent_text_color);
    }
    public getSectionBgColor(isDefault: boolean = false): Color{
        if(!this.userThemeParams || isDefault){
            return new Color(this.defaultThemeParams.section_bg_color);
        }
        return new Color(this.userThemeParams.section_bg_color);
    }
    public getSectionHeaderTextColor(isDefault: boolean = false): Color{
        if(!this.userThemeParams || isDefault){
            return new Color(this.defaultThemeParams.section_header_text_color);
        }
        return new Color(this.userThemeParams.section_header_text_color);
    }
    public getSectionSeparatorColor(isDefault: boolean = false): Color{
        if(!this.userThemeParams || isDefault){
            return new Color(this.defaultThemeParams.section_separator_color);
        }
        return new Color(this.userThemeParams.section_separator_color);
    }
    public getSubtitleTextColor(isDefault: boolean = false): Color{
        if(!this.userThemeParams || isDefault){
            return new Color(this.defaultThemeParams.subtitle_text_color);
        }
        return new Color(this.userThemeParams.subtitle_text_color);
    }
    public getDestructiveTextColor(isDefault: boolean = false): Color{
        if(!this.userThemeParams || isDefault){
            return new Color(this.defaultThemeParams.destructive_text_color);
        }
        return new Color(this.userThemeParams.destructive_text_color);
    }
}